div.container-panier{
  div.bloc-page-panier{
    h2{
      font-weight: 800;
      font-size: 27px;
      color: $color-primary;
      margin-bottom: 40px;
      text-align: left;
    }
  }
  div.content-panier-infos{
    background-color: $color-black;
    color: $color-white;
    div.content-img-date{
      padding: 0;
      h4.date-event{
        padding: 5px;
        background-color: $color-secondary;
        text-align: center;
        font-weight: 800;
        color: $color-primary;
      }
    }
    div.content-commande{
      padding: 20px;
      p{
        font-size: 16px;
        span{
          font-weight: 600;
        }
      }
    }
    div.content-price{
      margin: 0 0 0 auto;
      border: 1px solid #fff;
      width: 60px;
      height: 60px;
      p{
        font-size: 16px;
        font-weight: 800;
        text-align: center;
        padding-top: 20px;
      }
    }

  }
}
