body{
  font-family: 'Montserrat', sans-serif;
}

header.header{
  .container{
    padding-top:15px;
    padding-bottom: 15px;
    .row{
      .logo{

      }
      .menu-nav{
        nav.nav{
            ul{
              text-align: right;
              list-style-type: none;
              margin: 0 0 0 auto;
              li:nth-last-child(2){
                background-color: $color-secondary;
              }
              li:hover{
                border: 2px solid $color-secondary;
                padding: 0 5px;
              }
              li{
                font-size: 15px;
                font-weight: 600;
                display: inline-block;
                color: $color-primary;
                padding: 0 5px;
                a{
                  text-decoration: none;
                  color: inherit;
                }
              }
            }
        }
      }
    }
  }

}

@media screen and (max-width: 992px){
  header.header{
    .container{
      .row{
        .menu-nav{
          nav.nav{
              ul{
                li{
                  font-size: 13px;
                }
              }
          }
        }
      }
    }

  }
}

@media screen and (max-width: 768px){
  header.header{
    .container{
      .row{
        .menu-nav{
          nav.nav{
            text-align: right;

          }
        }
      }
    }

  }
}


@media screen and (max-width: 510px){
  header.header{
    .container{
      padding-top:15px;
      padding-bottom: 15px;
      .row{
        .logo{

        }
        .menu-nav{
          nav.nav{
            text-align: right;
              ul{
                list-style-type: none;
                li{
                  font-size: 13px;
                  display: block;
                  padding: 0 5px;

                }
              }
          }
        }
      }
    }

  }
}
