/* -------- affichage desktop -------- */
div.container-slider{
  padding: 0;
  div.slider-home{
    div.text-slider{
      color: $color-white;
      position: absolute;
      z-index: 1000;
      margin-left: 7%;
      margin-top: 15%;

      h2.title-slider{
        font-size: 50px;
      }
      p.text-slider{
        font-size: 27px;
        padding: 20px 0;
      }
      hr.separation-hr{
        width: 30px;
        border: 2px solid $color-secondary;
        color: $color-white;
        margin: 0 0 auto 0;
      }
      a.btn-slider{
        color: $color-white;
        font-weight: 800;
        font-size: 16px;
        padding: 10px 20px;
        border: 2px solid $color-secondary;
      }
    }

    img{
    }
    .img-deco-home{
      text-align: center;
      margin: 0 auto;
      img.deco-home{
        margin-top: -25px;
        width: 50px;
        height: 50px;
      }
    }
  }

}

/* -------- affichage à partir de 1400px -------- */
@media screen and (max-width: 1400px){
  div.container-slider{
    padding: 0;
    div.slider-home{
      div.text-slider{
        margin-top: 10%;
        h2.title-slider{
          font-size: 40px;
        }
      }
    }
  }
}

/* -------- affichage à partir de 992px -------- */
@media screen and (max-width: 992px){
  div.container-slider{
    padding: 0;
    div.slider-home{
      div.text-slider{
        h2.title-slider{
          font-size: 30px;
        }
        p.text-slider{
          font-size: 20px;
        }
        a.btn-slider{
          font-size: 14px;
        }
      }
    }
  }
}
/* -------- affichage à partir de 768px -------- */
@media screen and (max-width: 768px){
  div.container-slider{
    padding: 0;
    width: 100%;
    div.slider-home{
      div.text-slider{
        h2.title-slider{
          font-size: 20px;
        }
        p.text-slider{
          font-size: 16px;
        }
        a.btn-slider{
          font-size: 10px;
          padding: 5px 10px;

        }
      }
    }
  }
}
/* -------- affichage à partir de 510px -------- */
@media screen and (max-width: 510px){
  div.container-slider{
    padding: 0;
    width: 100%;
    div.slider-home{
      div.text-slider{
        margin-left: 5%;
        margin-top: 5%;
        h2.title-slider{
          font-size: 17px;
        }
        p.text-slider{
          font-size: 15px;
          padding: 10px 0;
        }
        a.btn-slider{
          font-size: 10px;
          padding: 5px 10px;
          background-color: rgba(0, 0, 0, .5);
        }
      }
    }
  }
}
