/* -------- affichage desktop -------- */
div.container-newsletter{
  background-color: $color-complementary;
  div.content-formulaire{
    padding: 30px 40px;
    text-align: center;
    margin: 0 auto;
    form{
      label{
        color: $color-white;
        font-size: 20px;
        font-weight: 800;
        padding-right: 30px;
      }
      input.champ{
        height:30px;
        width:350px;
        border: 0;
        padding: 5px 15px;
        margin: 0;
      }
      input.send{
        background-color: $color-primary;
        color: $color-white;
        font-size: 15px;
        height: 30px;
        padding: 5px 25px;
        margin: 0;
        border: 0;
        box-shadow: none;
      }
    }
  }
}

/* -------- affichage à partir de 992px -------- */
@media screen and (max-width: 992px){
  div.container-newsletter{
    padding-top: 160px;
    div.content-formulaire{
      padding: 20px 30px;
      text-align: center;
      margin: 0 auto;
      form{
        margin: 0 auto;
        text-align: center;
        input.champ{
          width:250px;
          margin-bottom: 10px;
        }
        input.send{
          margin: 0 auto;
          display: block;
        }
      }
    }
  }

}
